<template>
  <div class="login-container">
    <!-- 系统名称和图片 -->
    <div class="system-header">
      
      
       <img :src="logo" alt="Logo" class="system-logo" />
      
      
      <h2 class="system-name">代码智能加固系统</h2>
    </div>

    <!-- 登录表单 -->
    <el-form :model="form" :rules="rules" ref="loginForm" label-width="0px" class="login-form">
      <!-- 用户名 -->
      <el-form-item prop="username">
        <el-input
          v-model="form.username"
          placeholder="请输入用户名"
          clearable
        >
          <template #prefix>
            <el-icon><User /></el-icon> <!-- 用户名图标 -->
          </template>
        </el-input>
      </el-form-item>

      <!-- 密码 -->
      <el-form-item prop="password">
        <el-input
          v-model="form.password"
          placeholder="请输入密码"
          show-password
          clearable
        >
          <template #prefix>
            <el-icon><Lock /></el-icon> <!-- 密码图标 -->
          </template>
        </el-input>
      </el-form-item>

      <!-- 按钮组 -->
      <el-form-item>
        <div class="button-group">
          <el-button type="primary" @click="submitForm" class="login-btn">登录</el-button>
          <el-button @click="resetForm" class="reset-btn">重置</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { ElMessage } from "element-plus";
import { User, Lock } from "@element-plus/icons-vue"; // 引入图标
import apiRequest from "@/utils/request";

export default {
  components: {
    User, // 用户图标
    Lock, // 密码图标
  },
  data() {
    return {
      logo: require('@/assets/logo2.jpg'), // 引用 assets 文件夹下的 logo
      form: {
        username: "",
        password: "",
      },
      message: "", // 保存提示信息
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 15, message: "用户名长度在 3 到 15 个字符之间", trigger: "blur" },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 6, message: "密码长度至少为 6 个字符", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    async submitForm() {
      this.$refs.loginForm.validate(async (valid) => {
        if (!valid) {
          return ElMessage.error("请完整填写表单");
        }

        try {
          const response = await apiRequest.post("api/login.php", this.form);
          console.log("Response data:", response);
          if (response.status === "success") {
            const token = response.token;
            const userid = response.userid;
            const username = response.username;
            localStorage.setItem('auth_token', token);
            localStorage.setItem('Userid', userid);
            localStorage.setItem('username', username);
            ElMessage.success("登录成功，即将跳转...");
            setTimeout(() => {
              this.$router.push("/home");
            }, 1000);
          } else {
            this.message = response.message || "未知错误";
            ElMessage.error(this.message);
          }
        } catch (error) {
          console.error("Login request error:", error);
          if (error.response && error.response.data) {
            this.message = error.response.data.message || "未知错误";
          } else {
            this.message = "登录请求失败，请检查网络或联系管理员。";
          }
          ElMessage.error(this.message);
        }
      });
    },
    resetForm() {
      this.$refs.loginForm.resetFields();
      this.message = "";
    },
  },
};
</script>

<style scoped>
/* 确保 html 和 body 高度为 100% */
html,
body {
  height: 100%;
  margin: 0;
}

/* 父容器：垂直水平居中 */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f5f5f5;
  margin: 0;
  flex-direction: column;
}

/* 系统名称和图片 */
.system-header {
  text-align: center;
  margin-bottom: 0px;
}

.system-logo {
  width: 120px; /* 固定大小，适合页面 */
  height: 120px;
  border-radius: 50%; /* 使logo变圆形 */
  background: linear-gradient(45deg, rgba(255, 255, 255, 0) 50%, rgba(0, 0, 0, 0.1) 100%); /* 边缘渐变效果 */
  padding: 10px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2); /* 添加阴影效果，增强融入背景的感觉 */
  object-fit: cover; /* 保证图片不会拉伸，自动适应 */
}

.system-name {
  font-size: 36px;
  color: #333;
  margin-top: 10px;
}


/* 表单样式 */
.login-form {
  width: 400px;
  padding: 20px;
  background: #ffffff;
  border-radius: 8px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
}

/* 按钮组：水平居中 */
/* 按钮组：水平居中 */
.button-group {
  display: flex;
  justify-content: center;
  gap: 8px;
  width: 100%; /* 确保按钮组宽度占满整个父容器 */
}

/* 将重置按钮推到右侧 */
.reset-btn {
 
  width: 60px;
}

/* 按钮样式：宽度一致 */
.login-btn {
 
  width: 60px;
}

</style>
